@font-face {
  font-family: "Pixel text";
  src: url("../../assets/fonts/pixel-txt.ttf") format("truetype");
}

body {
  height: 100vh;
  font-family: "Pixel text";
  font-size: 1.4rem;
  background-color: #777;
  background-image: url("../../assets/img/game-theme-bckg.png");
  background-size: cover;
  background-blend-mode: multiply;
}

.row.mb2 {
  position: relative;
}

.btn {
  width: 100%;
  margin-bottom: 25px;
  background-color: #303030;
  border: 1px solid #444;
  color: #fff !important;
  box-shadow: none;
  cursor: auto;
  transition: 0.3s;
}

.btn:focus {
  box-shadow: none;
}

.btn:active {
  border: 1px solid #666 !important;
}

.btn-next {
  background-color: #00bc8c;
  cursor: pointer;
}

.btn-next:hover {
  background-color: #00efb2;
}
