@font-face {
  font-family: "Pixel font";
  src: url("../../assets/fonts/pixel-logo.ttf") format("truetype");
}

.header {
  margin-top: 0.5rem;
  flex-direction: column;
}

.top-panel {
  margin: 15px 0;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-radius: 0.25rem;
  background: #303030;
}

.top-panel > * {
  padding: 0 20px;
}

.top-panel h5 {
  font-size: 2rem;
}

.header .logo {
  font-family: "Pixel font";
  font-size: 4rem;
  text-transform: uppercase;
}

.logo-highlight {
  color: #b415ac;
}

.score {
  line-height: 55px;
}

.header ul.pagination {
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  margin-bottom: 25px;
  border-radius: 5px;
  overflow: hidden;
}

.header ul li {
  flex: auto;
  text-align: center;
}

.header ul li a {
  cursor: auto;

  border-radius: 0 !important;
}

.page-item .page-link {
  background-color: #008966;
  border-color: #007c5d;
}

.page-item.active .page-link {
  background-color: #00bc8c;
  border-color: #007c5d;
}

@media (max-width: 410px) {
  .header .logo {
    font-size: 3rem;
  }
}